import React, { useState, useEffect } from 'react';
import axios from 'axios';
import {
  Box,
  Button,
  Skeleton,
  ButtonGroup,
  LinearProgress,
} from '@mui/material';

function Anime() {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);
  const [tags, setTags] = useState('');

  useEffect(() => {
    setLoading(true);
    axios({
      method: 'GET',
      baseURL: 'https://api.waifu.im',
      url: `/search/?include_tags=${tags}`,
    })
      .then(({ data }) => {
        setData(data.images);
      })
      .catch((err) => console.dir(err))
      .finally(() => setLoading(false));
  }, [tags]);

  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <h2>Anime Pictures</h2>
      <ButtonGroup
        color="secondary"
        sx={{ marginBottom: '30px' }}
        variant="contained"
        aria-label="outlined primary button group"
      >
        <Button onClick={() => setTags('waifu')}>Waifu</Button>
        <Button onClick={() => setTags('selfies')}>Selfies</Button>
        <Button onClick={() => setTags('uniform')}>Uniform</Button>
        <Button disabled onClick={() => {}}>
          NSFW
        </Button>
      </ButtonGroup>
      {loading && <LinearProgress color="success" />}
      {!!data && data.length > 0 ? (
        data.map((waifu) => {
          return (
            <Box
              key={waifu.signature}
              bgcolor={waifu.dominant_color}
              sx={{
                width: '90%',
                maxWidth: '600px',
                minHeight: '600px',
                textAlign: 'center',
              }}
            >
              <img
                style={{ width: '100%', padding: '20px' }}
                src={waifu.url}
                alt={waifu.image_id}
              />
            </Box>
          );
        })
      ) : (
        <Skeleton
          variant="rectangular"
          sx={{ width: '90%', maxWidth: '600px', height: '600px' }}
        />
      )}
    </Box>
  );
}
export default Anime;
