import React from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Avatar,
  Box,
  Button,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemText,
  Drawer,
  Divider,
  Typography,
} from '@mui/material';
import { IconReact } from './svg.js';
import '../App.css';
import { Outlet } from 'react-router-dom';
import FormatQuoteIcon from '@mui/icons-material/FormatQuote';
import HomeIcon from '@mui/icons-material/Home';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import DynamicFeedIcon from '@mui/icons-material/DynamicFeed';
import FilterIcon from '@mui/icons-material/Filter';
import CelebrationIcon from '@mui/icons-material/Celebration';
import MenuRoundedIcon from '@mui/icons-material/MenuRounded';

function Header(props) {
  return (
    <>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'space-between',
          alignItems: 'center',
          marginBottom: '30px',
          padding: '10px',
          width: '100%',
          height: 'auto',
          bgcolor: 'primary.main',
          color: 'primary.contrastText',
        }}
      >
        <IconButton
          color="inherit"
          onClick={props.onclick}
          aria-label="menu"
          className="rotate"
          disableRipple
          sx={{ float: 'start', mr: 2 }}
        >
          <IconReact />
        </IconButton>
        <TemporaryDrawer />
      </Box>

      <Outlet />
    </>
  );
}

export default Header;

function TemporaryDrawer() {
  const [state, setState] = React.useState({
    left: false,
  });

  const toggleDrawer = (anchor, open) => (event) => {
    if (
      event.type === 'keydown' &&
      (event.key === 'Tab' || event.key === 'Shift')
    ) {
      return;
    }

    setState({ ...state, [anchor]: open });
  };

  const navigate = useNavigate();
  const openInNewTab = (url) => {
    const newWindow = window.open(url, '_blank', 'noopener,noreferrer');
    if (newWindow) newWindow.opener = null;
  };
  const list = (anchor) => (
    <Box
      sx={{ width: anchor === 'top' || anchor === 'bottom' ? 'auto' : 290 }}
      role="presentation"
      onClick={toggleDrawer(anchor, false)}
      onKeyDown={toggleDrawer(anchor, false)}
    >
      <List>
        <Typography variant="h4" sx={{ m: '30px' }}>
          React APP
        </Typography>
        <Divider sx={{ marginBottom: '30px' }} />
        <ListItem
          disablePadding
          onClick={() => {
            navigate('/');
          }}
        >
          <ListItemButton>
            <HomeIcon />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Home" />
          </ListItemButton>
        </ListItem>
        <ListItem disablePadding>
          <ListItemButton
            onClick={() => {
              navigate('/products');
            }}
          >
            <ShoppingCartIcon />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Shopping" />
          </ListItemButton>
        </ListItem>
        <ListItem
          disablePadding
          onClick={() => {
            navigate('/anime');
          }}
        >
          <ListItemButton>
            <FilterIcon />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Anime" />
          </ListItemButton>
        </ListItem>
        <ListItem
          disablePadding
          onClick={() => {
            navigate('/quotes');
          }}
        >
          <ListItemButton>
            <FormatQuoteIcon />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Quotes" />
          </ListItemButton>
        </ListItem>
        <ListItem
          disablePadding
          onClick={() => {
            navigate('/posts');
          }}
        >
          <ListItemButton>
            <DynamicFeedIcon />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Posts" />
          </ListItemButton>
        </ListItem>
        <ListItem
          disablePadding
          onClick={() => {
            navigate('/jokes');
          }}
        >
          <ListItemButton>
            <CelebrationIcon />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Jokes" />
          </ListItemButton>
        </ListItem>
        <Divider sx={{ marginTop: '30px' }} />

        <ListItem sx={{}}>
          <ListItemButton
            disabled
            variant="secondary"
            color="default"
            onClick={() =>
              openInNewTab('https://karthik1915.github.io/karthikeyan.aboutme/')
            }
          >
            <Avatar
              sx={{ width: 24, height: 24 }}
              alt="Karthikeyan"
              src="https://i.pinimg.com/736x/c2/23/24/c22324e7088934283545439601b82536.jpg"
            />
            <ListItemText sx={{ marginLeft: '10px' }} primary="Author" />
          </ListItemButton>
        </ListItem>
      </List>
    </Box>
  );

  return (
    <div>
      {['left'].map((anchor) => (
        <React.Fragment key={anchor}>
          <Button onClick={toggleDrawer(anchor, true)}>
            <MenuRoundedIcon color="secondary" />
          </Button>
          <Drawer
            anchor={anchor}
            open={state[anchor]}
            onClose={toggleDrawer(anchor, false)}
          >
            {list(anchor)}
          </Drawer>
        </React.Fragment>
      ))}
    </div>
  );
}
