import React, { useState, useEffect, useLayoutEffect } from 'react';
import axios from 'axios';
import { gsap } from 'gsap';
import {
  LinearProgress,
  Box,
  Button,
  ButtonGroup,
  CircularProgress,
  Paper,
  Typography,
} from '@mui/material';

function Jokes() {
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState(null);
  const [category, setCategory] = useState('Any');
  const [answer, setAnswer] = useState(false);

  useLayoutEffect(() => {
    let ctx = gsap.context(() => {
      gsap.to('#Paper', { rotationX: 720 });
    }, Box);
    return () => ctx.revert(); // cleanup
  }, [category]);

  useEffect(() => {
    setLoading(true);
    axios({
      method: 'GET',
      baseURL: 'https://v2.jokeapi.dev',
      url: `/joke/${category}?type=twopart`,
    })
      .then(({ data }) => {
        setData(data);
      })
      .catch((err) => console.dir(err))
      .finally(() => setLoading(false));
  }, [category]);
  return (
    <Box
      sx={{
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
      }}
    >
      <h2>Jokes</h2>
      <ButtonGroup
        color="secondary"
        sx={{ marginY: '30px' }}
        variant="contained"
        aria-label="outlined primary button group"
      >
        <Button
          onClick={() => {
            setAnswer(false);
            setCategory('Any');
          }}
        >
          Any
        </Button>
        <Button
          onClick={() => {
            setAnswer(false);
            setCategory('Coding');
          }}
        >
          Coding
        </Button>
        <Button
          onClick={() => {
            setAnswer(false);
            setCategory('Spooky');
          }}
        >
          Spooky
        </Button>

        <Button
          onClick={() => {
            setAnswer(false);
            setCategory('Misc');
          }}
        >
          Misc
        </Button>
      </ButtonGroup>
      {loading && <LinearProgress color="success" />}
      {!!data ? (
        <Box ref={Box} sx={{ width: '70%' }}>
          <Paper
            onClick={() => setAnswer(true)}
            id="Paper"
            elevation={4}
            sx={{ padding: '40px' }}
          >
            <Typography
              variant="h6"
              sx={{ textDecoration: 'underline', mb: '5px' }}
            >
              {data.category}
            </Typography>
            {!answer ? (
              <Typography variant="h5">{data.setup}</Typography>
            ) : (
              <Typography variant="h5">{data.delivery}</Typography>
            )}
          </Paper>
        </Box>
      ) : (
        <CircularProgress />
      )}
    </Box>
  );
}

export default Jokes;
